import React from "react";

import arrowDown from "assets/img/icons/gray/arrow-down.svg";
import {
  compose,
  StateHandler,
  StateHandlerMap,
  withStateHandlers,
} from "recompose";
import {
  ExpansionWrapper,
  Icon,
  MobileRow,
  MobileTitle,
  ProductDescription,
  ProductTitle,
  TitleText,
} from "./elements";

interface StateProps {
  open: boolean;
}

export type PageStateHandlerProps = StateHandlerMap<StateProps> & {
  toggleOpenState: StateHandler<StateProps>;
};

interface CategoryProps {
  /* eslint-disable no-undef */
  category: {
    prop: string | JSX.Element;
    brex: string | JSX.Element;
    AMEX: string | JSX.Element;
  };
  index: number;
  open?: boolean;
  toggleOpenState?: (event: React.MouseEvent) => void;
}

type AllPageProps = CategoryProps & StateProps & PageStateHandlerProps;

const _Category = ({
  category,
  index,
  open,
  toggleOpenState,
}: CategoryProps) => (
  <MobileRow>
    <MobileTitle onClick={toggleOpenState}>
      <Icon alt="arrow down" src={arrowDown} />
      <TitleText>{`0${index + 1}`}</TitleText>
      <TitleText>{category.prop}</TitleText>
    </MobileTitle>
    {open && (
      <ExpansionWrapper>
        <ProductTitle>Brex Business Card</ProductTitle>
        <ProductDescription>{category.brex}</ProductDescription>
        <ProductTitle>Amex Business Platinum Card</ProductTitle>
        <ProductDescription>{category.AMEX}</ProductDescription>
      </ExpansionWrapper>
    )}
  </MobileRow>
);

const Category = compose<AllPageProps, CategoryProps>(
  withStateHandlers<StateProps, PageStateHandlerProps>(
    { open: false },
    {
      toggleOpenState: ({ open }) => () => ({
        open: !open,
      }),
    },
  ),
)(_Category);

export default Category;
