import styled from "@emotion/styled";
import { padding } from "polished";
import { gothamFonts } from "styles/fonts";
import colors from "styles/colors";
import { breakPoints } from "components/Grid";

interface BreakdownRowProps {
  isTopRow?: boolean;
  isBottomRow?: boolean;
}
interface BreakdownCellProps {
  isHeader?: boolean;
  isLabel?: boolean;
  isHighlighted?: boolean;
  isExtendedTop?: boolean;
  isExtendedBottom?: boolean;
  noBorder?: boolean;
  index?: boolean;
}

export const Container = styled("div")({
  width: "100%",
  backgroundColor: colors.white,
});

export const Title = styled("div")({
  ...gothamFonts.h2,
  color: colors.black,
  fontWeight: 600,
  paddingBottom: 24,
  maxWidth: 760,
  textAlign: "center",
  [breakPoints.medium]: {
    ...gothamFonts.h3,
    textAlign: "left",
  },
});

export const Subtitle = styled("div")({
  ...gothamFonts["body-l-light"],
  color: colors.black,
  textAlign: "center",
  paddingTop: 8,
  maxWidth: 750,
  [breakPoints.medium]: {
    textAlign: "left",
  },
});

export const CenteredContainer = styled("div")({
  width: "100%",
  alignItems: "center",
  display: "flex",
  flexDirection: "column",
  ...padding(90, 0, 40, 0),
  [breakPoints.medium]: {
    alignItems: "flex-start",
  },
});

export const Breakdown = styled("div")({
  marginTop: 80,
  width: "100%",
  ...padding(40, 0),
  backgroundColor: colors.offWhite,
  [breakPoints.medium]: {
    display: "none",
  },
});

export const BreakdownRow = styled("div")<BreakdownRowProps>(
  ({ isTopRow = false, isBottomRow = false }) => [
    {
      display: "flex",
      [breakPoints.medium]: {
        display: "none",
      },
    },
    isTopRow && {
      alignItems: "flex-end",
    },
    isBottomRow && {
      alignItems: "flex-start",
    },
  ],
);

const regularPadding = 20;
const extraPadding = 20;
const minHeight = 140;

export const BreakdownCell = styled("div")<BreakdownCellProps>(
  ({
    isHeader = false,
    isLabel = false,
    isHighlighted = false,
    isExtendedTop = false,
    isExtendedBottom = false,
    noBorder = false,
    index = false,
  }) => [
    {
      display: "flex",
      width: "30%",
      background: colors.offWhite,
      color: isHighlighted ? colors.white : colors.gray1,
      alignItems: "center",
      justifyContent: "center",
      textAlign: "center",
      fontSize: 14,
      ...padding(regularPadding),
    },
    isHeader && {
      ...gothamFonts.h3,
      minHeight,
      color: isHighlighted ? colors.white : colors.black,
      alignItems: "flex-start",
    },
    {
      borderBottom: !(isHeader || isHighlighted || noBorder)
        ? `1px solid ${colors.lightGray}`
        : "1px solid transparent",
    },
    isHighlighted && {
      background: colors.primary,
    },
    isExtendedTop && {
      minHeight: minHeight + extraPadding,
      paddingTop: regularPadding + extraPadding,
    },
    isExtendedBottom && {
      paddingBottom: regularPadding + extraPadding,
      boxShadow: "0 3px 5px rgba(0, 0, 0, .1)",
    },
    index && {
      ...gothamFonts.h5,
      width: "10%",
      borderBottom: "1px solid transparent",
      justifyContent: "flex-end",
      color: colors.black,
      textTransform: "uppercase",
    },
    isLabel && {
      ...gothamFonts.h5,
      color: colors.black,
      justifyContent: "flex-start",
      textAlign: "left",
      textTransform: "uppercase",
    },
  ],
);

