import React from "react";

import { Row, Column } from "components/Grid";
import Category from "./category";

import {
  Container,
  Title,
  Subtitle,
  CenteredContainer,
  Breakdown,
  BreakdownRow,
  BreakdownCell,
  // mobile
  MobileWrapper,
} from "./elements";

import { data } from "./messages";

const Desktop = () => (
  <Breakdown>
    <BreakdownRow isTopRow>
      <BreakdownCell isHeader index />
      <BreakdownCell isHeader />
      <BreakdownCell isHeader isHighlighted isExtendedTop>
        Brex Business Card
      </BreakdownCell>
      <BreakdownCell isHeader>Amex Business Platinum Card</BreakdownCell>
    </BreakdownRow>
    {data(false).map((item, i) => {
      const isLastRow = i === data(false).length - 1;
      return (
        <BreakdownRow key={`breakdown-row-${item.prop}`}>
          <BreakdownCell noBorder={isLastRow} index>
            {`0${i + 1}`}
          </BreakdownCell>
          <BreakdownCell noBorder={isLastRow} isLabel>
            {item.prop}
          </BreakdownCell>
          <BreakdownCell noBorder={isLastRow} isHighlighted>
            {item.brex}
          </BreakdownCell>
          <BreakdownCell noBorder={isLastRow}>{item.AMEX}</BreakdownCell>
        </BreakdownRow>
      );
    })}

    <BreakdownRow isBottomRow>
      <BreakdownCell noBorder index />
      <BreakdownCell noBorder />
      <BreakdownCell noBorder isHighlighted isExtendedBottom />
      <BreakdownCell noBorder />
    </BreakdownRow>
  </Breakdown>
);

const Mobile = () => (
  <MobileWrapper>
    {data(true).map((category, index) => (
      <Category category={category} index={index} key={`category-${index}`} />
    ))}
  </MobileWrapper>
);

const BreakdownComponent = () => (
  <>
    <Container id="breakdown">
      <Row padding={60} gutterWidth={26}>
        <Column span={12}>
          <CenteredContainer>
            <Title>
              Why you should choose Brex over Amex Business Platinum
            </Title>
            <Subtitle>
              There’s more to business than just travel. Below, you’ll see all
              the ways that Brex beats the Amex Business Platinum card.
            </Subtitle>
            <Desktop />
          </CenteredContainer>
        </Column>
      </Row>
    </Container>
    <Mobile />
  </>
);

export default BreakdownComponent;
